import React from "react";

class GameMaster extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {}

  render() {
    return (
      <div className="mx-auto" style={{ width: "800px" }}>
        <div id="content">
          <div class="spinner-border" role="status">
            <span class="sr-only">Loading...</span>
          </div>
        </div>
      </div>
    );
  }
}

export default Employees;
